.ExperienceItem {
    padding: 10px 10px;
    box-sizing: border-box;
    border-top: 1px solid #b7b3b3;
    border-left: 1px solid #b7b3b3;
    position: relative;
}

.TimeIcon {
    font-size: 30px;
    position: absolute;
    top: -20px;
    left: -19px;
    background-color: #ffffff;
    padding: 4px;
    color: #1296db;
    font-weight: 900;
}
